	module.exports = {"data":{"post":{"html":"<p><b>He's walking</b><br />\nNot just \"two steps to get from chair to chair because it's too much trouble to get down and crawl\", this is \"get up by myself and take six steps just because I feel like it\". And Grandma and Grandpa were here to see it. The world has just become a tiny bit more unsafe.</p>\n<p><b>Early</b><br />\nGrandma and Grandpa have been here a week. Mama's had a busy work week, so Christopher and I have done our best to entertain them.</p>\n<p>No pictures, because we've been playing with a new Canon MV300i video camera. I've knocked myself out trying to load video into the PC. I think the Firewire card is bad.</p>","fields":{"title":"Halftime Report","date":"27 Apr 2001","url":"https://papascott.de/2001-04-27-halftime-report/"}}},"pathContext":{"slug":"/2001-04-27-halftime-report/","prev":{"fields":{"title":"Grandma and Grandpa","date":"20 Apr 2001","slug":"/2001-04-20-grandma-and-grandpa/"}},"next":{"fields":{"title":"First Steps","date":"01 May 2001","slug":"/2001-05-01-first-steps/"}}}}

